@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: kalameh;
  src: url(../public/fonts/Kalameh-Regular.ttf);
}

@font-face {
  font-family: khatkhati;
  src: url(../public/fonts/Khatkhati.ttf);
}

html {
  scroll-behavior: smooth;
}

:root {
  --color1: #9EA7B4;
  --color2: #707B8A;
  --color3: #535D6B;
  --color4: #3F4A59;
  --color5: #2A3341;
  --color6: #1D242F;
  --color7: #CFD6DF;
  --color8: #E3E8EE;
  --color9: #E2E7ED;
}

@keyframes Opening {
  0% {
    opacity: 0;
    top: 50px;
  } 100% {opacity: 1; top: 0;}
}

@media only screen and (max-width: 925px) {
  body .navigation {
    display: none;
  }
  body .innernav {
    justify-content: flex-end;
  }
  body .drp-btn {
    display: block;
  }
  body .logo-title {
    font-size: 25px;
    padding: 10px;
  }
  body .logo-footer {
    font-size: 20px;
    padding: 30px 10px;
  }
  body .Scrolltop-btn {
    width: 50px;
    height: 50px;
  }
  body #copy-right1 {
    display: none;
  }
  body #copy-right2 {
    display: block;
  }
  .body .welcome-img {
    margin-top: -100px;
    width: 2000px;
  }
  .body #welcome {
    font-size: 20px;
  }
  body .welcome-box {
    display: block;
  }
  body .welcome2 {
    display: block;
  }
  body .welcome1 {
    display: none;
  }
}

body {
  background-color: var(--color9);
  color: var(--color5);
  font-family: kalameh;
}

::-webkit-scrollbar {
  width: 10px;
  background-color: var(--color7);
}

::-webkit-scrollbar-thumb {
  background-color: var(--color1);
  border-radius: 10px;
}

@keyframes appear1 {
  0% {
    top: 20px;
    opacity: 0;
  } 100% {
    top: 0;
    opacity: 1;
  }
}

@keyframes move1 {
  0% {
    right: -84%;
  } 100% {
    right: 0;
  }
}

@keyframes move2 {
  0% {
    right: 84%;
  } 100% {
    right: 0;
  }
}

@keyframes Scrolldown {
  0% ,100% {
    opacity: 0.5;
    top: 0;
  }
  50% {
    opacity: 1;
    top: 20px;
  }
}

.navbar {
  width: 100%;
  height: 100px;
  background-color: #e3e8ee7c;
  backdrop-filter: blur(10px);
  display: flex;
  justify-content: center;
  padding-top: 18px;
  position: fixed;
  transition: 500ms;
  z-index: 100;
}

.logo-title {
  font-size: 36px;
  font-family: khatkhati;
  cursor: pointer;
  color: var(--color6);
  display: none;
  transition: 200ms;
  position: relative;
}

.logo-title2 {
  font-size: 38px;
  font-family: khatkhati;
  cursor: pointer;
  color: var(--color6);
  display: none;
  transition: 200ms;
  position: relative;
  opacity: 0;
}

.drp-btn {
  width: 50px;
  height: 50px;
  background-color: var(--color8);
  border-radius: 10px;
  border: 3px solid var(--color2);
  color: var(--color5);
  font-size: 20px;
  position: relative;
  display: none;
  margin: 0 20px;
}

.seemore-btn {
  width: 120px;
  height: 45px;
  background-color: var(--color6);
  color: var(--color8);
  border: 3px solid var(--color6);
  border-radius: 10px;
  transition: 200ms;
}

.seemore-btn:hover {
  background-color: var(--color8);
  color: var(--color6);
}

.welcome-box {
  height: 600px;
  overflow: hidden;
}

.welcome-img {
  width: 600px;
  margin-top: -200px;
}

.welcome-title {
  font-size: 40px;
  font-family: khatkhati;
  cursor: pointer;
  color: var(--color6);
  transition: 200ms;
  position: relative;
}

#inner-nav {
  transition: 1s;
  position: relative;
  justify-content: space-between;
}

.navigation {
  display: flex;
  margin-top: 18px;
  gap: 30px;
  transition: 220ms;
  position: relative;
}

.navigation li {
  width: auto;
  font-size: 18px;
  cursor: pointer;
  transition: 200ms;
  border-bottom: 20px solid inherit;
  font-weight: 500;
  height: 10px;
  padding: 0 15px;
}

.navigation li:hover {
  border-bottom: 2px solid var(--color6);
  height: 35px;
  transform: translateY(-5px);
}

.title-decor {
  width: 15px;
  height: 50px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  background-color: var(--color6);
}

.exp-list {
  list-style-type: disc;
  margin-right: 20px;
}

.exp-list li {
  margin-top: 5px;
  cursor: default;
  transition: 200ms;
  padding: 10px;
  border-radius: 10px;
}

.exp-list li:hover {
  background-color: var(--color7);
}

.art-temp {
  width: 253px;
  height: 300px;
  border: 2px solid var(--color1);
  color: var(--color3);
  text-align: center;
  padding: 30px 10px;
  border-radius: 15px;
  cursor: default;
  transition: 200ms;
}

.art-temp:hover ,.book-temp:hover {
  border: 2px solid var(--color6);
  color: var(--color6);
  transform: translateY(-5px);
}

.art-btn {
  width: 60px;
  height: 60px;
  background-color: var(--color6);
  color: var(--color8);
  transition: 200ms;
  border: 2px solid var(--color6);
  border-radius: 10px;
}

.art-btn:hover {
  background-color: var(--color8);
  color: var(--color6);
}

.book-temp {
  width: 318px;
  height: 420px;
  border: 2px solid var(--color1);
  color: var(--color3);
  text-align: center;
  padding: 30px 10px;
  border-radius: 10px;
  cursor: default;
  transition: 200ms;
}

.book-temp img {
  margin-top: -20px;
  border-radius: 6px;
}

.drp {
  width: 100%;
  height: 200%;
  display: none;
  position: relative;
  background-color: #00000000;  
  margin-top: 10px;
}

.drp li {
  width: 100%;
  height: 100px;
  text-align: center;
  border-bottom: 2px solid #fff;
  padding-top: 35px;
  background-color: inherit;
  backdrop-filter: blur(10px);
}

@keyframes drpOpen {
  0% {
    height: 0;
    top: 1000px;
  }
  100% {
    height: 100%;
    top: 0;
  }
}

.footer {
  margin-top: 100px;
  border-top: 2px solid var(--color7);
  background-color: var(--color6);
  width: 100%;
  height: auto;
}

.logo-footer {
  color: var(--color8);
  font-family: khatkhati;
  font-size: 40px;
  padding: 30px;
  cursor: default;
}

.Scrolltop-btn {
  background-color: var(--color6);
  color: var(--color7);
  width: 70px;
  height: 70px;
  border-radius: 10px;
  border: 2px solid var(--color7);
  margin: 25px;
  transition: 200ms;
}

.Scrolltop-btn:hover {
  background-color: var(--color8);
  color: var(--color6);
}





